import React, { useEffect, useState } from 'react';

/* global kakao */

const UserLocationMarker = ({ map, MeMarker }) => {
    const [userLocation, setUserLocation] = useState(null);

    //     
    const getUserLocation = () => {
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(
                (position) => {
                    const latitude = position.coords.latitude;
                    const longitude = position.coords.longitude;
                    setUserLocation({ latitude, longitude });
                },
                (error) => {
                    console.error('Error fetching user location:', error);
                }
            );
        } else {
            console.error('Geolocation is not supported by this browser.');
        }
    };

    useEffect(() => {
        //    
        getUserLocation();
    }, []);

    useEffect(() => {
        //       
        if (userLocation && map) {
            const userLatLng = new kakao.maps.LatLng(userLocation.latitude, userLocation.longitude);

            const marker = new kakao.maps.Marker({
                map: map,
                position: userLatLng,
                title: ' '
            });

            //     
            marker.setImage(
                new kakao.maps.MarkerImage(MeMarker, new kakao.maps.Size(50, 50))
            );

            //     
            map.panTo(userLatLng);
        }
    }, [userLocation, map, MeMarker]);

    return null; //      UI 
};

export default UserLocationMarker;
